import { AfterViewInit, Component, OnInit } from '@angular/core';
import { AuthService } from '../core/auth.service';
import { PetService } from '../core/pet.service';
import { UtilityService } from '../core/utility.service';
import { ServiceProviderService } from '../core/service-provider.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-services-of-provider',
  templateUrl: './services-of-provider.page.html',
  styleUrls: ['./services-of-provider.page.scss'],
})
export class ServicesOfProviderPage implements OnInit {

	  hideMe:any;
 profileData:any;
 services:any;
  constructor(private petService: PetService,
    	private utility: UtilityService,
    	private auth: AuthService,
    	private service: ServiceProviderService,
    	private router: ActivatedRoute) { }

  ngOnInit() {
  	
  }


  ionViewDidEnter(){

  	this.getProfileData();
  }
hide(val){
	if(val == this.hideMe){
	this.hideMe = 0;
	}
	else{
	this.hideMe = val;
	}  
}

getProfileData() {
		console.log('profile')
    	let userid = JSON.parse(localStorage.getItem('userObj'))._id;
    	this.auth.getProfile({ id: userid }).subscribe((res) => {
      		console.log(res);
      		this.profileData = res;
      		this.getServicesData();
      		this.utility.dismiss();
    	});
  	}

  	getServicesData() {

    	let userid = JSON.parse(localStorage.getItem('userObj'))._id;

    	this.service.getServicesOfProvider(userid).subscribe((res) => {
	      console.log(res);
	      this.services = res;
	    });
    	
  	}

}

